.Projects{
    height: 100%;
    min-height: 100vh;
    margin-right: 50px;
    margin-left: 50px;
    border-radius: 20px;
    background-image: url(../files/background3.jpg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    color: snow;
}


.Header{
    background-image:url("../files/background2.jpg");
    background-attachment: fixed;
    background-position: center;
    width: fit-content;
    padding: 50px;
    height: 100px;
    margin: auto;
    margin-bottom: 50px;
    text-align: center;
    border-top:0;
    border-left: 2px black solid;
    border-right: 2px black solid;
    border-bottom: 2px black solid;
    border-radius: 0 0 15px 15px;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.Header h1{
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
}

 .Projects h2{
    max-width: 500px;
    margin:auto;
    text-align: center;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    color: wheat;
}

.project-div{
 margin-bottom: 50px;
 margin-left: 30px;
 margin-right: 30px;
}
.project-caption{
    max-height: fit-content;
    max-width: 700px;
    margin: auto;
    border-top: 0;
    border-left: 1px solid wheat;
    border-right: 1px solid wheat;
    border-bottom: 1px solid wheat;
    border-radius: 0 0 20px 20px ;
    text-align: center;
    background-color: #18191A;
}
.project-caption button{
    margin-top: 10px;
    margin-bottom: 20px;
    width: 40%;
    min-width: 150px;
    min-height: 40px;
    border-radius: 20px;
    color: snow;
    background-image:url("../files/background2.jpg");
    background-attachment: fixed;
    background-position: center;
}
.project-caption a, .project-caption a:hover{
    width: 100%;
    text-decoration: none;
    color: snow;
}
.project-caption button:hover{
    border-color: wheat;
    width:45%;
    height:45px;
}
.project-caption button:hover::after{
    content: "";
    display: block;
    margin: 0 auto; 
    width: 0%; 
    border-bottom: 1px solid wheat; 
    animation: buttonHover 2s;
    animation-fill-mode: forwards;
}

.project-caption p{
    margin-left: 10px;
    margin-right: 10px;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: large;

}
.github{
    margin-top: 20px;
    margin-bottom: 40px;
}
.githublogo{
   
}
.gridGithub{
    margin: auto;
    width: 300px;
    display: grid;
    grid-template-columns: 1fr 1fr;

}
.gridGithub h3{
    margin-top: 30%;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
.gridGithub a{
    text-decoration: none;
    color: snow;
}
.gridGithub:hover a{
    text-decoration: none;
    color: wheat;
}
.gridGithub h3:hover{
    margin-top: 30%;
    color: wheat;
    cursor: pointer;
}

.technology{
    color: wheat;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}




@keyframes buttonHover{
    0%{
        width: 5%;
      }
    25%{
        width: 80%;
      }
    100%{
        width: 50%;
    }
    
}


@media only screen and (max-width: 600px) {

    .Projects{
        width:100vw;
        margin-left: 0;
        margin-right: 0;
    }


}