.AboutMe{
    padding:50px;
    height: 100%;
    min-height: 1000px;
    width: 100vw;
    background: transparent;
    overflow-y: hidden;
}
.Main-Row{
    height: 100vh;
    align-items: center;
}
.AboutMe p {
    color: snow;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 20px;
}
.AboutMe h2 {
    color: wheat;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-style: italic;
}
.text-column{
        text-align: left;
}
#img{
    border: 2px solid wheat;
    max-height: 700px;
}