.Contact{
    height: 100vh;
    margin-left: 50px;
    margin-right: 50px;
    margin-bottom: 50px;
    border-radius: 20px;
    background-image: url(../files/background3.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    color: snow;
    overflow: hidden;
    text-align: center;
}
.Contact h1{
   font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
   font-style: oblique;
   color: wheat;
}
label{
    margin-top: 2px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: large;
    font-weight: 400;
}
.contact-form{
    width: 50%;
    height: auto;
    margin: auto;
    border: wheat 1px solid;
    border-radius: 20px;
    padding: 50px;
    text-align: center;
    display: grid;
    background:url("../files/background2.jpg");
    background-size: cover;
    background-attachment: fixed;
}
.contact-form>input{
    text-align: center;
    width: 50%;
    margin: auto;
    border-radius: 10px;
}
.contact-form textarea{
    width: 80%;
    max-height: 150px;
    margin:auto;
}
.submit{
    width:20%;
    margin: auto;
    margin-top: 10px;
}


@media only screen and (max-width: 600px) {

    .Contact{
        width:100vw;
        margin-left: 0;
        margin-right: 0;
        margin-bottom: 0;
    }

    .contact-form{
        width:100%
    }


}