body,html {
    margin: 0;
    scroll-behavior: smooth;
    overflow-x: hidden;
    overflow-y: scroll;
}
.App{
    position: absolute;
    background-image:url("../files/background2.jpg");
    background-size: cover;
    background-attachment: fixed;
    background-position: center;
    scroll-behavior: smooth;
}
::-webkit-scrollbar {
    display: none;
}