.Skills{
    height: 100%;
    min-height: 100vh;
    width: 100vw;
    background: transparent;
    color: snow;
}
.header-column{
    text-align: center;
}
.header-column h1{
    margin-top: 50px;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 60px

}
.image{
    margin-top: 20px;
    max-width: 200px;
    max-height: 200px;
}
.image:hover{
    opacity: 80%;
    cursor: pointer;
}
.column{
    text-align: center;
    margin-bottom: 40px;
    color: wheat;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
.info-div{
    width: fit-content;
    margin: auto;
    text-align: center;
    padding: 10px;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: large;
}


